export default {
	data() {
		return {
			modalName: null,
			bookStatus: 0,
			curRoom: null,
			status: null,
			swiperList: [{
				id: 0,
				type: 'image',
				url: '../../static/girls/1.jpg'
			},{
				id: 0,
				type: 'image',
				url: '../../static/girls/1.jpg'
			},{
				id: 0,
				type: 'image',
				url: '../../static/girls/1.jpg'
			}]
		};
	},
	computed: {
		style(){

				var CustomBar= this.CustomBar;
				var style = `top:${CustomBar}px; margin-top: 10px; width: 70px; height: 70px; border-radius: 100%; position: absolute; z-index: 999; right: 0`;
				return style
		
		}
	},
	onLoad(options) {
		console.log(options);
		this.status = options.status == 0 ? 0 : 1
	},
	methods:{
		MineGoOrderList(){
			this.$emit("MineGoOrderList")
		},
		viewPresent(){
			uni.navigateTo({
				url: '../present-history/present-history'
			})
		},
		editInfo(){
			uni.navigateTo({
				url: '../swiper/swiper'
			})
		},
		hideModal(){
			this.modalName = null
		},
		changeCall(){
			this.modalName = 'call'
		},
		openModal(e){
			this.modalName = e.currentTarget.dataset.name
		},
		goBook(e){
			uni.navigateTo({
				url: "../book/book"
			})
		},
		changeBookStatus(){
			if(this.bookStatus != 0){
				this.bookStatus = 0
			}
		},
		changeBookStatusB(){
			if(this.bookStatus != 1){
				this.bookStatus = 1
			}
			
		},
		changeRoom(e){
			// console.log(e);
			this.curRoom = e.currentTarget.dataset.index
		}
	}
}
